const showScheduleEveryDay = () => {
  const title = "Soccer News";
  const opt = {
    silent: true,
    tag: "welcome",
    icon: "clipart-ball-logo.png",
    badge: "clipart-ball-logo.png",
    body:
      "Hi, welcome to Soccer News App. Let's explore your favourite team in here!",
    data: {
      dateOfArrival: Date.now(),
      primaryKey: 2,
    },
    actions: [
      {
        action: "yes-welcome",
        title: "Yes!",
      },
    ],
  };
  return { title, opt };
};

const requestPermission = async () => {
  if ("Notification" in window) {
    const result = await Notification.requestPermission();
    if (result === "denied") {
      console.log("Notification not allowed");
      return;
    } else if (result === "default") {
      console.log("the user closes the notification dialog box");
      return;
    }
    const reg = await navigator.serviceWorker.getRegistration();
    reg.showNotification(
      showScheduleEveryDay().title,
      showScheduleEveryDay().opt
    );
  }

  const urlBase64ToUint8Array = (base64String) => {
    const padding = "=".repeat((4 - (base64String.length % 4)) % 4);
    const base64 = (base64String + padding)
      .replace(/-/g, "+")
      .replace(/_/g, "/");
    const rawData = window.atob(base64);
    const outputArray = new Uint8Array(rawData.length);
    for (let i = 0; i < rawData.length; i++) {
      outputArray[i] = rawData.charCodeAt(i);
    }
    return outputArray;
  };

  const subscribe = async () => {
    try {
      const opt = {
        userVisibleOnly: true,
        applicationServerKey: urlBase64ToUint8Array(
          "BNEcw7y2yXKcGEBeOV9AvvD1TdWo-MiGA7BCFKvVIRfvhf2gqFkK5VoTgEyWheORRD4UZqQWZpfznjhax9uj8jE"
        ),
      };
      const reg = await navigator.serviceWorker.getRegistration();
      const subscribe = await reg.pushManager.subscribe(opt);
      console.log("endpoint subs: ", subscribe.endpoint);
      console.log(
        "p256dh subs: ",
        btoa(
          String.fromCharCode.apply(
            null,
            new Uint8Array(subscribe.getKey("p256dh"))
          )
        )
      );
      console.log(
        "auth key subs: ",
        btoa(
          String.fromCharCode.apply(
            null,
            new Uint8Array(subscribe.getKey("auth"))
          )
        )
      );
    } catch (error) {
      console.error("cannot subscribe: ", error.message);
    }
  };

  if ("PushManager" in window) {
    subscribe();
  }
};

export { requestPermission };
