import "regenerator-runtime";
import { Workbox } from "workbox-window";
import { requestPermission } from "./assets/js/register.js";
import { loadNav, loadPage } from "./assets/js/appShell.js";
import "./assets/css/style.css";
import "./assets/materialize/js/materialize.min.js";

if ("serviceWorker" in navigator) {
  window.addEventListener("load", async () => {
    await registerSW();
    requestPermission();
  });
} else {
  console.log("service worker is not supported. please update your browser.");
}

document.addEventListener("DOMContentLoaded", function () {
  let sidenav = document.querySelector(".sidenav");
  M.Sidenav.init(sidenav);
  loadNav(sidenav);

  let page = window.location.hash.substr(1);
  if (page === "") page = "home";
  if (page.split("?id=")) page = page.split("?")[0];
  loadPage(page);
});

const registerSW = () => {
  return new Promise(() => {
    let toastReload = `<span>Reload for a new content.</span><button class="btn-flat toast-action reload"><i class="material-icons medium">autorenew</i></button>`;
    const wb = new Workbox("/sw.js");
    wb.addEventListener("waiting", () => {
      M.toast({ html: toastReload });
      document.querySelector(".reload").addEventListener("click", () => {
        wb.addEventListener("controlling", () => {
          window.location.reload();
        });
        wb.messageSW({ type: "SKIP_WAITING" });
      });
    });
    wb.register();
  });
};
